import './_empty-cart.scss';
const EmptyCart=({handleToggleCart})=> {
    return (
      <article className='empty-cart'>
        <div className="empty-cart__img"></div>
        <p className="empty-cart__report">  .</p>
        <p className="empty-cart__advice">  ,    .</p>
        <button className="empty-cart__btn btn" type="button" onClick={handleToggleCart}> </button>
      </article>
    )
  }
export default EmptyCart