import { useState, useEffect } from 'react';

import './_order-sum.scss';

const OrderSum = ({totalSum, totalDiscount}) => {
    return (//    ?
      <table className="full-cart__sum order-sum">
        <tbody>
          <tr>
            <td className='order-sum__name' >:</td>
            <td className='order-sum__discount'>{totalDiscount}</td>
          </tr>
          <tr>
            <td className='order-sum__name'>:</td>
            <td className='order-sum__total'>{totalSum}</td>
          </tr>
        </tbody>
      </table>
    );
  };
  
  export default OrderSum;